
import Toast from 'vux/src/components/toast/index.vue'

export default {
components: {
Toast
},
data () {
return {
messages: [],
hasNext: true,
count: 1,
error: false,
message: ''
}
},
methods: {
getlist () {
if (!this.hasNext) {
this.showError('没有更多信息')
this.hasNext = false
} else {
this.$http.post('/api/user/message/list?pers=10&page=' + this.count).then((response) => {
this.$vux.loading.hide()
this.messages = this.messages.concat(response.data.data)
this.hasNext = response.data.hasNext
if (this.hasNext) {
this.count++
}
})
}
},
showError (msg) {
this.message = msg
this.error = true
}
},
created () {
this.getlist()
},
filters: {
onlyData: function (value) {
return value.substring(0, 10)
}
}
}



