
import Toast from 'vux/src/components/toast/index.vue'

export default{
components: {
Toast
},
created () {
this.getlist()
},
data () {
return {
products: [],
hasNext: true,
count: 1,
error: false,
message: ''
}
},
methods: {
getlist () {
if (!this.hasNext) {
this.showError('没有更多信息')
this.hasNext = false
} else {
this.$http.post('/api/user/activity/list?pers=10&page=' + this.count).then((response) => {
this.$vux.loading.hide()
this.products = this.products.concat(response.data.data)
this.hasNext = response.data.hasNext
if (this.hasNext) {
this.count++
}
console.log(this.products)
})
}
},
showError (msg) {
this.message = msg
this.error = true
}
},
filters: {
onlyData: function (value) {
return value.substring(0, 10)
},
describeOver: function (value) {
if (value[0].length > value[1]) {
return value[0].substring(0, value[1]) + '...'
} else {
return value[0]
}
}
}
}



