  export default{
    created () {
//      this.$vux.loading.show({
//        text: '加载中'
//      })
      this.listRe()
//      console.log(this.$root.Bus)
    },
    data () {
      return {
        recommend: []
      }
    },
    methods: {
      listRe () {
        this.$http.get('/api/activity/list?p=1&pers=3').then((response) => {
          this.$vux.loading.hide()
          if (response.data.status === 10000) {
            this.recommend = response.data.data
          }
        })
      },
      toTravl () {
        this.$router.push({
          path: '/travel'
        })
      }
    },
    watch: {
      $route () {
        document.documentElement.scrollTop = document.body.scrollTop = 0
      }
    },
    filters: {
      onlyData: function (value) {
        return value.substring(0, 10)
      },
      describeOver: function (value) {
        if (value[0].length > value[1]) {
          return value[0].substring(0, value[1]) + '...'
        } else {
          return value[0]
        }
      }
    }
  }



